.getting-intro-catalog__title {
  font-weight: bold;
  font-size: 18px;

  text-transform: uppercase;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 30px;
  }

  @media (min-width: @desktop-width) {

    // padding-right: 250px;
    // padding-left: 250px;
  }
}

.getting-intro-catalog__intro {
  color: @brown;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  margin-top: 30px;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-top: 35px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 35px;
  }
}

.getting-intro-catalog__break {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}
