.page-header {
  background-color: @white;
  display: flex;
  flex-direction: column;

  // outline: 5px solid rgba(234, 0, 255, 0.8);
  // outline-offset: -5px;

  width: 100%;
}

.page-header__logo-image {
  position: relative;

  // outline: 5px solid rgba(0, 255, 94, 0.8);
  // outline-offset: -5px;
  // opacity: 0;
  z-index: 100;
  width: 101.7px;
  height: 84px;


  @media (min-width: @tablet-width) {
    width: 101.6px;
    height: 84px;
  }

  @media (min-width: @desktop-width) {
    width: 186.6px;
    height: 96.43px;
  }
}

.page-header__line {
  height: 56px;

  // outline: 5px solid rgba(255, 0, 0, 0.8);
  // outline-offset: -5px;

  background-image: url("../../img/layer.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  order: 2;
  width: 100%;
  position: relative;
  display: block;

  @media (min-width: @tablet-width) {
    position: absolute;
    // transform: translateX(-50%);
    // left: 50%;

    .page-header__logo {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }

  @media (min-width: @desktop-width) {
    // height: 96.43px;
    position: absolute;
    max-width: 1200px;
  }
}


